import { Form, Modal, Button, FloatingLabel, InputGroup } from "react-bootstrap"
import { useRef, useState } from "react"

import { useBudgets } from "./contexts/BudgetsContext"
import { useFixedExpenses } from "./contexts/FixedExpensesContext"
import { useVariableExpenses } from "./contexts/VariableExpensesContext"

import { parseDate } from "../utils/dateUtils"

const AddExpenseModal = ({show, handleClose}) => {
    const {budgets} = useBudgets()
    const { saveFixedExpense } = useFixedExpenses()
    const { saveVariableExpense } = useVariableExpenses()

    const categoryRef = useRef()
    const typeRef = useRef()
    const budgetRef = useRef()
    const amountRef = useRef()
    const commentsRef = useRef()
    const txnDateRef = useRef()

    function handleSubmit(e) {
        e.preventDefault()
        console.log("Expense added.")
        console.log(parseDate(txnDateRef.current.value))
        console.log(categoryRef.current.value)
        console.log(budgetRef.current.value === 0 ? "No Budget Found" : budgetRef.current.value)
        console.log(amountRef.current.value)
        handleClose()
    }

    return (
        <Modal show={show} onHide={handleClose} >
            <Form onSubmit={handleSubmit} >
                <Modal.Header closeButton>
                    <Modal.Title>New Expense</Modal.Title>
                </Modal.Header>
                
                <Modal.Body >
                    <Form.Group className="">
                        <FloatingLabel 
                            label="Expense Type">
                            <Form.Select className="" ref={typeRef}>
                                <option value="variable" >One Time Expense</option> 
                                <option value="fixed" >Recurring Expense</option> 
                            </Form.Select>
                        </FloatingLabel>
                    </Form.Group>
                    <Form.Group className="my-3" controlId="txnd">
                        <FloatingLabel 
                            label="Transaction Date">
                            <Form.Control 
                                className=""
                                ref={txnDateRef}
                                type="date"
                                name="txnd"
                                />
                        </FloatingLabel>
                    </Form.Group>
                    <Form.Group className="my-3">
                        <FloatingLabel 
                            label="Category">
                            <Form.Control 
                                className=""
                                defaultValue={"Uncategorized"}
                                ref={categoryRef}
                                type="text"/>
                        </FloatingLabel>
                    </Form.Group>
                    <Form.Group className="my-3">
                        <FloatingLabel
                            label="Budget">
                            <Form.Select className="" ref={budgetRef}>
                                {budgets.length ? budgets.map((budget) => (
                                    <option value={budget.id} key={budget.id}>{budget.monthYear}</option>
                                )) : <option value="0">No Budgets Found</option>}
                            </Form.Select>
                        </FloatingLabel>
                    </Form.Group>
                    <Form.Group className="my-3">
                        <Form.Label>Amount</Form.Label>
                        <InputGroup>
                            <InputGroup.Text >$</InputGroup.Text>
                            <Form.Control 
                                type="number"
                                ref={amountRef}
                                defaultValue={0.00}
                                required
                                min={0}
                                step={0.01}/>
                        </InputGroup>
                    </Form.Group>
                    <Form.Group className="my-3">
                        <FloatingLabel
                            label="Comments">
                            <Form.Control 
                                as="textarea"
                                rows={4}
                                className=""
                                ref={commentsRef}/>
                        </FloatingLabel>
                    </Form.Group>
                    <div className="d-flex justify-content-end my-3">
                        <Button variant="primary" type="submit">
                            Add
                        </Button>
                    </div>
                </Modal.Body>
            </Form>
        </Modal>
      )
}

export default AddExpenseModal